/* .icon */
.icon{
    background-image: url('../../../src/images/sprite/sprite.png');
    color: transparent;
    &_cancel{display: inline-block;}
}

/* .contents_wrap */
.contents_wrap{
    max-width: 480px;
    width: auto;
    margin: 0 auto;
    padding: 74px 40px 49px;
    box-sizing: border-box;
    &.dislike_menu .text_box,
    &.yesterday_menu .text_box{
        height: calc(100vh - 407px);
        min-height: 134px;
    } //.contents_wrap.dislike_menu .text_box, .contents_wrap.yesterday_menu .text_box
    &.guide .contents{
        height: calc(100vh - 211px);
        min-height: 377px;
    } // .contents_wrap.guide .contents, .contents_wrap.dislike_menu .contents, .contents_wrap.yesterday_menu .contents
    &.recommend_result .contents{
        height: calc(100vh - 284px);
        min-height: 1060px;
    } // contents_wrap.recommend_result .contents

    &.guide{
        overflow: hidden;
        .contents{
            min-height: 487px;
        }
        .swiper, swiper-container{
            overflow: unset;
            margin-left: -22px;
            counter-reset: num 0;
        }
    } // contents_wrap.guide

    /* line */
    .line{
        width: 100vw;
        height: 2.08vw;
        max-height: 10px;
        max-width: 480px;
        margin-left: -40px;
        background-color: #f6f6f6;
    }
}

/* title_wrap */
.title_wrap{
    font-size: 18px;
    margin: 39px 0 42px;
    &_subject{
        font-size: 30px;
        font-weight: 600;
        margin-bottom: 5px;
        &::after{
            background-position: -54.58vw -92px;
            width: 40px;
            height: 40px;
        } //.title_wrap_subject::after
    } // .title_wrap_subject
    .icon{
        display: inline-block;
        vertical-align: bottom;
        margin-left: 5px;
    }
    &.type1{
        color: #fff;
        font-weight: 500;
        font-size: 20px;
        letter-spacing: -1px;
        margin: 0;
        .title_wrap_subject{
            font-family: $ff_nanum;
            font-size: 48px;
            margin-bottom: 19.8px;
            font-weight: 900;
            .beta{
                display: inline-block;
                font-size: 13px;
                width: 46px;
                height: 20px;
                background-color: #fff;
                border-radius: 5px;
                color: #f97312;
                text-align: center;
                line-height: 20px;
                font-family: "NanumSquare", "san-serif";
                letter-spacing: -0.9px;
            }
        }
    } // .title_wrap.type1

    &.type2{
        margin: 39px 0 0;
        .title_wrap_subject{
            position: relative;
            font-size: 36px;
            line-height: 47px;
            .text{
                display: inline-block;
                box-shadow: 0 -24px 0 0 #fff5c0 inset;
                padding-bottom: 4px;
            }
        }
    } // .title_wrap.type2
}

/* contents */
.contents{
    box-sizing: border-box;
    .util{
        padding: 9px 0;
        .icon_arrow{
            display: inline-block;
            vertical-align: middle;
        }
    }
}

/* guide_slider */
.guide_slider{
    position: relative;
    width: 170px !important;
    min-height: 220px;
    box-sizing: border-box;
    border: solid 1px #ffcc4d;
    border-radius: 15px;
    background-color: #fff9df;
    padding: 37px 35px 37px;
    &::before{
        counter-increment: num 1;
        content: counter(num);
        display: block;
        position: absolute;
        font-family: $ff_nanum;
        font-size: 20px;
        color: #ffcc4d;
        top: 10px;
        left: 15px;
        font-weight: 900;
    } // .guide_slider::before
    .icon{
        position: absolute;
        top: 25px;
        left: 25.3px;
        &_box{
            position: relative;
            width: 90px;
            height: 90px;
            background-color: #fff;
            border-radius: 50px;
        }
        
    }
    &_text{
        font-size: 14px;
        text-align: center;
        margin-top: 17.8px;
    } // guide_slider_text
}


/* pagebar */
.pagebar{
    display: inline-block;
    width: 210px;
    height: 13px;
    background-color: #f5f5f5;
    border-radius: 50px;
    margin: 0 auto;
    vertical-align: middle;
    margin-left: 62px;
    &::before{
        content: '';
        display: block;
        width: 70px;
        height: 13px;
        background-color: #fea800;
        border-radius: 50px;
    } // .pagebar::before
    &.type1::before{
        width:133px;
    } // .pagebar.type1::before
    &.type2::before{
        width:210px;
    } // .pagebar.type12:before
}

/* recommend */
.recommend{
    width: 80vw;
    max-width: 385px;
    margin: 31px 0 0 54px;
    display: flex;
    flex-wrap: wrap;
    &_wrap::after{
        @include float;
    } // .recommend_wrap:after
    &_box{
        height: 252px;
        border-radius: 30px 0 0 30px;
        overflow:hidden;
    } // .recommend_box
    &_img{
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    } // .recommend_img
    &_title{
        font-size: 24px;
        font-weight: 600;
        margin-top: 21px;
    } // .recommend_title
    &_address{
        font-size: 14px;
        line-height: 28px;
        margin: 10px 70px 45px 0;
        &::after{
            @include float;
        } // .recommend_address::after
        &_title{
            float: left;
            width: 18vw;
            max-width: 56px;
            margin-right: 10px;
        } // .recommend_address_title
        &_text{
            float: left;
            max-width: 247px;
            width: 49vw;
            line-height: 20px;
            text-align: left;
        } // .recommend_address_text
        &_share{
            float: right;
            max-width: 247px;
            width: 51vw;
            margin-top: 26px;
            &::after{
                @include float;
            } // .recommend_address_share::after
            li{
                position: relative;
                float: left;
                width: 25vw;
                max-width: 115px;
                &:last-child::after{content: none;}
                // .recommend_address_share li:last-child:after
                &::after{
                    content: "";
                    display: block;
                    position: absolute;
                    top: 3px;
                    right: 18px;
                    width: 1px;
                    height: 20px;
                    background-color: #000;
                } // .recommend_address_share li::after
            }
            a{
                display: block;
                font-size: 14px;
                text-decoration: underline;
                text-align: left;
            }
        } // .recommend_address_share
    } // .recommend_address
    &_another{
        width: 100%;
        max-width: 400px;
        margin: 45px 0 48px;
        &_title{
            margin-bottom: 22px;
            font-size: 20px;
            font-weight: 600;
        } // .recommend_another_title
        &_wrap{
            display: flex;
        } // .recommend_another_wrap
        &_box{
            flex-basis: 50%;
        } // .recommend_another_box
        &_img{
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            &_box{
                width: 185px;
                max-height: 180px;
                height: 38vw;
                border-radius: 30px;
                overflow: hidden;
            } // .recommend_another_img_box
        } // .recommend_another_img
        &_text{
            font-size: 18px;
            margin-top: 10px;
        } // .recommend_another_text
    } // .recommend_another

}

/* wide_button */
.wide_button {
    width: 400px;
    height: 73px;
    display: block;
    margin:0 auto 15px auto;
    background-color: #fea800;
    border-radius: 15px;
    color: #fff;
    font-size: 24px;
    font-family: $ff_nanum;
    box-sizing: border-box;
    text-align: center;
    padding: 19px;
    font-weight: 900;
    line-height: 35px;
    &.type1{
        background-color: #fffae1;
        color: #fea800;
    } // .wide_button.type1
}

/* .text_box */
.text_box{
    .type{
        display: block;
        font-size: 14px;
        font-weight: 600;
        margin-bottom: 14px;
    }
    input[type="text"]{
        width: 380px;
        height: 34px;
        font-size: 24px;
        border-bottom: solid 2px #fea800;
        &::placeholder{
            color:#d9d9d9;
            font-family: $ff_base;
        } // .text_box input[type="text"]::placeholder
        &:focus + .text_box_focus{
           display: block;
        } // .text_box input[type="text"]:focus + .text_box_focus
    }
    &_focus{
        display: none;
        color: #fea800;
        font-size: 14px;
        margin-top: 9.6px;
        font-weight: 600;
    } // .text_box_focus
}

